import React from 'react';
import classes from './DoctorPage.css';
import {Link} from 'react-router-dom';
import {callTwo} from '../../callTwo';
import Error from '../../Components/Error/Error';
import Loader from '../../Components/Loader/Loader';
class DoctorPage extends React.Component{

    state = {
        loggedIn:true,
        loadingTitle : 'Loading',
        name : '',
        password : '',
        confrimPassword : '',
        specialization : '',
        email : '',
        loading :true,
        showError : false,
        errorMessage : "Password does not match. Please enter correct passowrd",
        errorTitle : "Invalid",
        buttonTitle : "OK"
    }
    componentDidMount(){
        this.setState({
            loading :false
        })
    }
    valueChangeHandler = (event , type) => {
        var tempState = {...this.state};
        tempState[type] = event.target.value;
        this.setState({
           ...tempState
        })
    }

    submitHandler = (event) => {
        event.preventDefault();
        if(this.state.confirmPassword !== this.state.password){
            this.setState({
                showError : true,
                errorMessage : "Password does not match. Please enter correct passowrd",
                errorTitle : "Invalid",
            })
            return;
        }
        this.setState({
            loading : true,
            loadingTitle : "Uploading your details"
        })
        callTwo({
            method : 'POST',
            url : '/signup',
            data : {
                name : this.state.name,
                email : this.state.email,
                password : this.state.password,
                specialization : this.state.specialization
            },

        }).then(res => {
            this.setState({
                showError : true,
                errorMessage : "Verfication link send to registered email address",
                errorTitle : "Success",
                buttonTitle : "Go to Homepage",
                loading :false
            })
          
            
        }).catch(err => {
            console.log(err);
            this.setState({
                loading : false,
                showError : true,
                errorTitle : "Error",
                errorMessage : err
            })
        })
    }

    render(){
        return this.state.loading ? <Loader title = {this.state.loadingTitle} /> :(
            <div className = {classes.container}>
                <div className = {classes.home_left}>
                        <form method = "none" className = {classes.form}>
                            <div className = {classes.headingContainer}>
                                <h3>Register </h3>
                            </div>
                            <div className = {classes.inputContainer}>
                                <label className = {classes.label}>Name</label>
                                <input type = "text" name = "Name" className = {classes.input} placeholder = "Enter your name"
                                    onChange = {(event) => this.valueChangeHandler(event , 'name') }
                                />
                            </div>
                            <div className = {classes.inputContainer}>
                                <label className = {classes.label}>Email</label>
                                <input type = "email" name = "Name" className = {classes.input} placeholder = "Enter your email"
                                    onChange = {(event) => this.valueChangeHandler(event , 'email') }

                                />
                            </div>
                            <div className = {classes.inputContainer}>
                                <label className = {classes.label}>Password</label>
                                <input type = "password" name = "Name" className = {classes.input} placeholder = "Enter your password"
                                    onChange = {(event) => this.valueChangeHandler(event , 'password') }

                                />
                            </div>
                            <div className = {classes.inputContainer}>
                                <label className = {classes.label}>Confirm Password</label>
                                <input type = "password" name = "Name" className = {classes.input} placeholder = "Confrim your password"
                                    onChange = {(event) => this.valueChangeHandler(event , 'confirmPassword') }

                                />
                            </div>
                            <div className = {classes.inputContainer}>
                                <label className = {classes.label}>Speciality</label>
                                <input type = "text" name = "Name" className = {classes.input} placeholder = "Speciality In" 
                                    onChange = {(event) => this.valueChangeHandler(event , 'specialization') }

                                />
                            </div>
                            <div className = {classes.inputContainer}>
                                <input type = "submit" name = "Name" value = "Register &#8594;" className = {classes.input} 
                                    onClick = {this.submitHandler}
                                />
                            </div>
                            <div className = {classes.links}>
                                <Link to = "/doctor/login">Already User? Login </Link>
                            </div>
                        </form>
                      


                    </div>
                    {this.state.showError ? <Error 
                    heading = {this.state.errorTitle}
                    message = {this.state.errorMessage}
                    buttonTitle = {this.state.buttonTitle}
                    onClickHandler = {() => {
                        if(this.state.buttonTitle === "Go to Homepage"){
                            this.props.history.push('/');
                        }
                        this.setState({
                            showError :false,
                            confrimPassword : '',
                            password : ''
                        })
                        
                    }}
                /> : null}
                    
            </div>
        )
    }
}


export default DoctorPage;