import React from 'react';
import classes from './Error.css';



const Error = props => {
    return (
        <div className = {classes.container}>
            <div className = {classes.errorBox}>
                <h2>{props.heading}</h2>
                <p>{props.message}</p>
                <button 
                    onClick = {props.onClickHandler}
                    className = {classes.button}
                >
                    {props.buttonTitle}
                </button>
            </div>
        </div>
    )
}


export default Error;