const inititalState = {
    email : null,
    doctorName : null,
    loggedIn: false,
    specialization : null,
    jwt : null,
    patientData : []
}

const reducer = (state = inititalState,  action) => {
    if(action.type === "LOGIN"){
        return {
            ...state,
            email : action.payload.email,
            loggedIn : true,
            doctorName : action.payload.doctorName,
            jwt : action.payload.jwt,
            patientData : action.payload.patientData
        }
    }else if(action.type === 'LOGOUT'){
        return {
            ...state,
            email : null,
            loggedIn : false,
            doctorName : null,
            jwt : null,
            patientData : [],
            specialization : null
        }
    }else if(action.type === 'LOADDATA'){
        return {
            ...state,
            patientData : action.payload.patientData
        }
    }
    else{
        return {
            ...state
        }
    }
}

export default reducer;