            'Chronic kidney disease (CKD) is defined by reduction in GFR, which in turn is associated with increased plasma ceratine and urea concentrations. As CKD progresses, plasma levels of both rise in tandem, so that BCR/UCR usually remains within normal limits in CKD.',
            "The serum creatinine level is an insensitive marker of GFR early in the course of CKD. A 33 percent decrease in GFR may raise the creatinine level from 0.8 to only 1.2 mg per dL (70.72 to 106.08 μmol per L). If the prior creatinine level is not known, this decrease in GFR may go unrecognized.",
            'Dysnatremia is the most common electrolyte abnormality in clinical practice. Clinically, normal serum sodium level in humans ranges within 135–144 mmol/L. Patients with CKD tend to develop dysnatremia mainly due to their diminished ability to maintain water homeostasis.',
            'People with chronic kidney disease (CKD) have a high risk for hyperkalemia, due in part to the effects of kidney dysfunction on potassium homeostasis. A recent review reports hyperkalemia frequency as high as 40-50% in people with chronic kidney disease compared to 2-3% in the general population.',
            "The target Hb we should achieve is >10 g/dl in dialysis patients and 11 g/dl in pre-dialysis patients. We recommend that the target Hb should not be >13 g/dl in all the CKD patients.",
