            'Also known as diabetic nephropathy or diabetic kidney disease (DKD), this condition is a result of vascular abnormalities that accompany diabetes and increases mortality risk. Furthermore, diabetes mellitus is a main risk factor for end-stage renal disease (ESRD), the most advanced stage of kidney disease.',
