import React from 'react';
import classes from './DoctorPage.css';
import {Link, Route} from 'react-router-dom';
import Register from './RegisterPage';
import Login from './LoginPage';
import HeartFrom from '../InputForm/InputForm';
import LungCancerForm from '../InputForm/BreastCancerForm';
import CKDForm from '../InputForm/CKDForm';
import Loader from '../../Components/Loader/Loader';
import { callTwo } from '../../callTwo';
import { connect } from 'react-redux';
import sprite from '../../Assets/svg/sprite.svg'


class DoctorPage extends React.Component{
    state = {
        loggendIn:false,
        disease : 'heartdisease',
        diseaseName : 'Heart Disease',
        validateScreen :false,
        loading : true,
        loadingTitle : "Welcome to Health Diagnostic System",
        values : -1
    }
    componentDidMount(){
        if(this.props.match.params.type === 'validate'){
            this.setState({
                validateScreen : true,
                loadingTitle : "Verifiying your details..."
            })
            callTwo({
                method : "GET",
                url : `/validate/${this.props.match.params.token}?id=${this.props.match.params.id}`,
            }).then(res => {
                console.log(res.data);
                this.setState({
                    loading : false
                })
            }).catch(err => {
                console.log(err);
                this.setState({
                    loading : false
                })
            })
        }else{
            this.setState({
                loading :false
            })
        }
    }
    
    updateDiseaseForm = (event) => {
       this.setState({
           disease : event.target.value,
           
       })
    }

    

    render(){
        console.log(this.props);

        const FormBlock = () => {
            if(this.state.disease === 'heartdisease'){
                return <HeartFrom target = {true}  />
            }else if(this.state.disease === 'cancerdisease'){
                return <LungCancerForm target = {true}  />
            }else if(this.state.disease === 'ckddisease'){
                return <CKDForm target = {true} />
            }else{
                return null
            }
        }

        const tableData = [];
        let ctr = 0;
        this.props.patientData.forEach((el, idx) => {
            if(el){
                ctr++;
                tableData.push((
                    <div key = {idx}>
                        <span>{ctr}</span>
                        <span>{el.type.toUpperCase()}</span>
                        <span><button
                            onClick = {() => {
                                this.setState({
                                    values : idx
                                })
                            }}
                        >Load Details</button></span>
                    </div>
                )) 
            }
           
        })

        return this.state.loading ? <Loader title = {this.state.loadingTitle} /> : (
            <div className = {classes.container}>
              {!this.props.loggedIn ?  <div className = {classes.background}>
                    <div className = {classes.content}>
                        {/* <img src = {Doctor}/> */}
                        {/* <p>Provide clinical patient data to improve the accuracy of the prediction of Heart , Lung cancer and Kidney diease so that preacaution can be made early as possible</p> */}
                        <div className = {classes.headerButtons}>
                            <Link to = "/doctor/login">Login</Link>
                            <Link to = "/doctor/register">Register</Link>
                    </div>
                    </div>
                   
               </div> : 
               <div>
               
               <div className = {classes.dataContainer}>
               <div className = {classes.detailsContainer}>
                    <button>
                        <svg>
                            <use xlinkHref = {`${sprite}#icon-user`}></use>
                        </svg>
                        <p>Hi, {this.props.doctorName}</p>
                    </button>
                    <button>
                        <svg>
                            <use xlinkHref = {`${sprite}#icon-link`}></use>
                        </svg>
                        <p>{this.props.email}</p>
                    </button>
                    {/* <button
                        onClick = {() => {
                           
                        }}
                    >
                        <svg>
                            <use xlinkHref = {`${sprite}#icon-pencil`}></use>
                        </svg>
                        <p>Add Patient Medical Data</p>
                    </button> */}
                    {/* <button>
                        <svg>
                            <use xlinkHref = {`${sprite}#icon-news`}></use>
                        </svg>
                        <p>My Patient Data</p>
                    </button> */}
               </div>
               <div className = {classes.patientData}>
                   
                    <div>
                        <h3>S.No</h3>
                        <h3>Disease</h3>
                        <h3></h3>
                    </div>
                    {tableData}
                   
               </div>
                        <div className = {classes.inputContainer}>
                             <label>Select Disease  </label>
                             <select defaultValue = {'select'} className = {classes.custom_select} onChange = {this.updateDiseaseForm}>
                                 <option value = "heartdisease" >Heart Disease</option>
                                 <option value = "ckddisease">Chronic Kidney Disease</option>
                                 <option value = "cancerdisease">Lung Cancer Disease</option>
                             </select>
                        </div>
                        <div id = "edit">
                      {FormBlock()}
                        </div>
                    </div>
                   
                </div>}
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        ...state
    }
}

export default connect(mapStateToProps , null)(DoctorPage);