import React from 'react';
import classes from './DoctorPage.css';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux'
import {login} from '../../Actions';
import {callTwo} from '../../callTwo';
import Error from '../../Components/Error/Error';
import Loader from '../../Components/Loader/Loader';
class DoctorPage extends React.Component{
    state = {
        loggedIn:true,
        loadingTitle : 'Loading',
        password : '',
        email : '',
        loading :true,
        showError : false,
        errorMessage : "Invalid email or password.",
        errorTitle : "Invalid",
        buttonTitle : "OK"
    }
    componentDidMount(){
        this.setState({
            loading :false
        })
    }
    valueChangeHandler = (event , type) => {
        var tempState = {...this.state};
        tempState[type] = event.target.value;
        this.setState({
           ...tempState
        })
    }

    submitHandler = (event) => {
        // console.log(event);
        event.preventDefault();
        this.setState({
            loading : true,
            loadingTitle : "Fetching your credentials"
        })
        callTwo({
            method : 'POST',
            url : '/signin',
            data : {
                name : this.state.name,
                email : this.state.email,
                password : this.state.password,
                specialization : this.state.specialization
            },

        }).then(async (res) => {
          console.log(res.data);
          callTwo({
            method : "GET",
            url : "/me",
            withCredentials : true,
            headers : {
                authorization : 'Bearer ' + res.data.token
            }
        }).then(async resp => {
            console.log("res" , resp);
            this.props.login({
                doctorName : resp.data.doctor.name,
                email : resp.data.doctor.email,
                jwt : res.data.token,
                specialization : resp.data.doctor.specialization,
                loggedIn : true,
                patientData : resp.data.patientData
            })
            await localStorage.setItem('jwt' ,res.data.token);
            setTimeout(() => {
                this.props.history.push('/doctor')
            } , 500)
        }).catch(err => {
            console.log(err);
            login({
                doctorName : null,
                email : null,
                jwt : null,
                specialization : null,
                loggedIn : false,
                patientData : null

            })
            
        })
         
        }).catch(err => {
            console.log(err);
            this.setState({
                loading : false,
                showError : true,
                errorTitle : "Invalid",
                errorMessage : "Invalid Email or Password"
            })
        })
    }
    render(){
        console.log(this.props);
        return this.state.loading ? <Loader title = {this.state.loadingTitle} /> : (
            <div className = {classes.container}>
                <div className = {classes.home_left}>
                        <form method = "none" className = {classes.form}>
                            <div className = {classes.headingContainer}>
                                <h3>Login </h3>
                            </div>
                            <div className = {classes.inputContainer}>
                                <label className = {classes.label}>Email</label>
                                <input type = "email" name = "Name" className = {classes.input} placeholder = "Enter your email"
                                    onChange = {(event) => this.valueChangeHandler(event , 'email') }

                                />
                            </div>
                            <div className = {classes.inputContainer}>
                                <label className = {classes.label}>Password</label>
                                <input type = "password" name = "Name" className = {classes.input} placeholder = "Enter your password"
                                    onChange = {(event) => this.valueChangeHandler(event , 'password') }

                                />
                            </div>
                            <div className = {classes.inputContainer}>
                                <input type = "submit" name = "Name" value = "Login &#8594;" className = {classes.input} 
                                    onClick = {this.submitHandler}
                                />
                            </div>
                            <div className = {classes.links}>
                                <Link to = "/doctor/register">New User? Register </Link>
                            </div>
                        </form>
                        {/* <div className = {classes.content}>
                        <img src = {Doctor}/>
                        <p>Provide clinical patient data to improve the accuracy of the prediction of Heart , Lung cancer and Kidney diease so that preacaution can be made early as possible</p>
                        <button>Provide Patient Medical Data &#8594;</button>
                        </div> */}


                    </div>
                    {this.state.showError ? <Error 
                    heading = {this.state.errorTitle}
                    message = {this.state.errorMessage}
                    buttonTitle = {this.state.buttonTitle}
                    onClickHandler = {() => {
                        this.setState({
                            showError :false,
                            email : '',
                            password : ''
                        })
                        
                    }}
                /> : null}
            </div>
        )
    }
}
const mapStateToProps = (state) => {
    return {
        ...state
    }
}

export default connect(mapStateToProps , {
    login
})(DoctorPage);