import React from 'react';
import classes from './PatientPage.css';
import {Link, Route} from 'react-router-dom';
import HeartFrom from '../InputForm/InputForm';
import LungCancerForm from '../InputForm/BreastCancerForm';
import CKDForm from '../InputForm/CKDForm';
import Refresh from '../../Assets/refresh.png';

class DoctorPage extends React.Component{

    state = {
        loggendIn:true,
        disease : 'select',
        refresh : false,
    }

    updateDiseaseForm = (event) => {
        this.setState({
            disease : event.target.value
        })
     }
    render(){

        const FormBlock = () => {
            if(this.state.disease === 'heartdisease'){
                return <HeartFrom target = {false} refresh = {this.state.refresh}/>
            }else if(this.state.disease === 'breastcancerdisease'){
                return <LungCancerForm target = {false} refresh = {this.state.refresh}/>
            }else if(this.state.disease === 'ckddisease'){
                return <CKDForm target = {false} refresh = {this.state.refresh}/>
            }else{
                return null
            }
        }

        return (
            <div className = {classes.container}>
               <div>
               <div className = {classes.dataContainer}>
                        <h2>Check whether you are healthy or not</h2>
                        <div className = {classes.inputContainer}>
                             <label style = {{margin : "0 1rem"}}>Select Disease </label>
                             <select defaultValue = {this.state.disease} className = {classes.custom_select} onChange = {this.updateDiseaseForm}>
                                 <option value = "select" className = {classes.option}>select</option>
                                 <option value = "heartdisease">Heart Disease</option>
                                 <option value = "breastcancerdisease">Breast Cancer Disease</option>
                                 <option value = "ckddisease">Chronic Kidney Disease</option>
                             </select>
                             <img src = {Refresh} 
                                 onClick = {() => {this.setState({
                                     refresh : new Date(Date.now()),
                                     disease : 'select'
                                 })}}
                             />
                        </div>
                        {FormBlock()}
                    </div>
                   
                </div>
            </div>
        )
    }
}


export default DoctorPage;