import React, { useState , useEffect } from 'react';
import classes from './Body.css';
import {BrowserRouter, Route, Link,NavLink} from 'react-router-dom';
import {Switch} from 'react-router';

import HomePage from '../HomePage/HomePage';
import DoctorPage from '../DoctorPage/DoctorPage';
import PatientPage from '../PatientPage/PatientPage';
import Logo from '../../Assets/logo.png';
import Login from '../DoctorPage/LoginPage';
import Register from '../DoctorPage/RegisterPage';
import sprite from '../../Assets/svg/sprite.svg';
import {connect} from 'react-redux'
import {login, logout} from '../../Actions';
import { callTwo } from '../../callTwo';
import Loader from '../../Components/Loader/Loader';
const active = {
    color : "#3489eb",
    fontWeight : 500,
}

const Body = props => {
    const [loading , chagneLoading] = useState(true);
    const [loadingTitle , chagneLoadingTitle] = useState('Welcome to Health Diagnostic System')
    const [showNav , toggleNav] = useState(false);
    useEffect(async() => {
        var jwt = await localStorage.getItem('jwt');
        // console.log("jwt " , jwt);
        callTwo({
            method : "GET",
            url : "/me",
            withCredentials : true,
            headers : {
                authorization : 'Bearer ' + jwt
            }
        }).then(res => {
            console.log("res" , res);
            props.login({
                doctorName : res.data.doctor.name,
                email : res.data.doctor.email,
                jwt : jwt,
                specialization : res.data.doctor.specialization,
                loggedIn : true,
                patientData : res.data.patientData
            })
            chagneLoading(false);
        }).catch(err => {
            console.log(err);
            login({
                doctorName : null,
                email : null,
                jwt : null,
                specialization : null,
                loggedIn : false,
                patientData : null

            })
            chagneLoading(false);

        })
    } ,[])
    return loading ? <Loader title = {loadingTitle} /> : (
        <BrowserRouter>
            <div className = {classes.container}>
                <header className = {classes.header}>
                    <div className = {classes.logo}>
                        <img src = {Logo} />
                        <div className = {classes.logo_heading}>
                            <h2>HealthDiagnostic</h2>
                            <h4>System</h4>
                        </div>
                    </div>
                    <div className = {classes.menuBox}>
                        <svg className = {classes.menu} onClick = {
                            () => {
                                toggleNav(!showNav);
                            }
                        }>
                            <use xlinkHref = {!showNav ? `${sprite}#icon-menu` : `${sprite}#icon-squared-cross`}/>
                        </svg>
                    </div>
                    <nav className = {`${classes.nav} ${showNav ? classes.show : null}`} onClick = {
                            () => {
                                toggleNav(!showNav);
                            }}>
                        <ul>
                            <li><NavLink to = "/" activeStyle = {active} exact>Home</NavLink></li>
                            <li><NavLink to = "/doctor" activeStyle = {active}>Doctor</NavLink></li>
                            <li><NavLink to = "/patient" activeStyle = {active}>Patient</NavLink></li>
                            <li><NavLink to = "/about-us" activeStyle = {active}>About Us</NavLink></li>
                        {props.loggedIn ? <button className = {classes.logoutButton} 
                        onClick = {() => {
                            props.logout({
                                doctorName : null,
                                email : null,
                                jwt : null,
                                specialization : null,
                                loggedIn : null,
                                patientData : []
                            })
                        }}
                        >Logout</button> : null}
                        </ul>
                    </nav>
                    {/* {!props.loggedIn ? <h2 className = {classes.doctorName}>Hi , {props.doctorName}</h2> : null} */}
                </header>
                <Switch>
                    <Route path = "/" exact component = {HomePage}/>
                    <Route path = "/patient" exact component = {PatientPage} />
                    <Route path = "/doctor/register" exact  component = {Register} />
                    <Route path = "/doctor/login" exact  component = {Login}/>   
                    <Route path = "/doctor" exact component = {DoctorPage} />
                    <Route path = "/doctor/:type/:token/:id" component = {DoctorPage} />

                </Switch>
                <footer className = {classes.footer}>
                    <div className = {classes.footer_left}>
                        <h4>© 2020 Health Diagnostic System, all rights reserved</h4>
                    </div>
                    <div className = {classes.footer_right}>
                        <h4>Made By - Jitendra Kumar Sharma ,Ravi Sanwal ,Tanya Chaudhary</h4>
                    </div>
                </footer>
            </div>
        </BrowserRouter>
    )
}
const mapStateToProps = (state) => {
    return {
        ...state
    }
}

export default connect(mapStateToProps,{
    login : login,
    logout : logout
})(Body);